/* Overlay Background */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensures it sits on top of other content */
  }
  
  /* Overlay Content Styling */
  .overlay-content {
    background-color: white;
    padding: 40px;
    border-radius: 10px;
    max-width: 500px;
    width: 90%; /* Responsive width, capped at 500px */
    text-align: center;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.25); /* Adds depth with a shadow */
  }
  
  .overlay-content h2 {
    font-size: 2rem; /* Larger text for headings */
    color: #0a192f; /* Dark blue, consistent with your main theme */
    margin-bottom: 20px;
  }
  
  .overlay-content p {
    font-size: 1.2rem; /* Comfortable reading size */
    color: #333; /* Dark grey for text */
    margin-bottom: 30px;
    text-align: justify; /* Ensures the text is evenly justified */
  }
  
  /* Benefits List */
  .benefits-list {
    text-align: left; /* Aligns the list to the left */
    margin-top: 20px;
    list-style-type: disc; /* Uses discs for bullets */
    padding-left: 20px; /* Indents the list */
    font-size: 1.1rem; /* Slightly larger font size for readability */
  }
  
  .benefits-list li {
    margin-bottom: 10px; /* Space between list items */
  }
  
  /* Close Button */
  .close-btn {
    padding: 10px 20px;
    background-color: #64ffda; /* Bright teal for visibility and design coherence */
    border: none;
    color: #0a192f;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease; /* Smooth transition for hover effect */
  }
  
  .close-btn:hover {
    background-color: #52d1b1; /* A slightly darker teal on hover */
  }
  