.services {
    padding: 100px 20px;
    background-color: #f9f9f9;
  }
  
  .services h2 {
    font-size: 2.5rem;
    font-weight: 700;
    color: #0a192f;
    margin-bottom: 40px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    border-bottom: 3px solid #64ffda;
    display: inline-block;
  }
  
  .service-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 40px;
  }
  
  .service-card {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    width: 250px;
    text-align: center;
    cursor: pointer;
    transition: transform 0.3s ease;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .service-card:hover {
    transform: translateY(-10px);
  }
  
  .service-card h3 {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  @media (max-width: 768px) {
    .services {
      padding: 80px 20px;
    }
  
    .service-cards {
      flex-direction: column;
      align-items: center;
    }
  
    .service-card {
      width: 90%;
      margin-bottom: 20px;
    }
  }
  .services h2 {
    font-family: 'Ruda', sans-serif; /* Use Ruda for section heading */
    font-size: 2.5rem;
    color: #0a192f;
  }
  
  .service-card h3 {
    font-family: 'Ruda', sans-serif; /* Use Ruda for service card titles */
    font-size: 1.5rem;
  }
  
  .service-card p {
    font-family: 'Roboto Slab', serif; /* Use Roboto Slab for descriptions */
    font-size: 1rem;
  }
  